import uniIcon from "@/components/uni-icon.vue"
import shopData from "./data.json"
export default {
	components: {
		uniIcon
	},
	data() {
		return {
		  shopList:[],
		  categoryActive:0,
		  itemObj: [],
		  scrollTop:0,
		  showPopup:false,
		  height:0,
		  p:[],
		  num:1,
		}
	},
	onLoad:function(){
		this.shopList=shopData.data;
		this.itemObj=this.shopList[0];
		this.height = Math.floor(uni.getSystemInfoSync().windowHeight);



	},
	methods: {
		categoryClick(index){
		 this.categoryActive=index;
		 this.itemObj = this.shopList[index];
		},
		showMiddlePopup(shop,index){
			this.p=this.itemObj.shopData[index];
			this.showPopup=true;
		},
		hidePopup(){
		this.showPopup=false;	
		},
		changetab(index,indexs){
		 this.p.kind[index].selected=indexs;
		},
		less(){
			if(this.num==1){
				uni.showToast({
				    title: '不能再少了',
				    duration: 1000,
					icon:"none",
				});
			}
			else{
			  this.num--;	
			}
		},
		more(){
		  if(this.num==999){
		  	uni.showToast({
		  	    title: '不能再多了',
		  	    duration: 1000,
		  		icon:"none",
		  	});
		  }
		  else{
			this.num++; 
		  }
		}
		
		
	}
}
